body {
  --header-height: 60px;
  --main-blue: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-size: 12px;
  display: flex;
}

.header__logo {
  height: 40px;
  margin: 0 0 0 50px;
  display: block;
}

.nav {
  margin: 0 50px 0 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  line-height: var(--header-height);
  color: #000;
  height: 100%;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--main-blue);
}

.is-active {
  color: var(--main-blue);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

